$time: .3s;
*{
    box-sizing: border-box;
}
.crypto{
    &-currencyes{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        
        .button{
            cursor: pointer;
            background-color: #ffb900;
            border: none;
            font-size: 20px;
            padding: 5px 10px;
            width: 200px;
            text-align: left;
            margin-bottom: 10px;
            transition: $time;
            outline: none;

            &.active{
                background-color: #ef5734;
                position: relative;
                
                &:before{
                    content: "";
                    position: absolute;
                    background-color: #ef5734;
                    width: 20px;
                    height: 3px;
                    top: 50%;
                    transform: translateY(-50%);
                    animation: 0.2s activeBtnAnim forwards;
                    
                    @keyframes activeBtnAnim {
                        from{
                            left: -100px;
                        }
                        to{
                            left: -20px;
                        }
                    }
                }
            }
        }
    }
    &-info-wrapper{
        max-width: 520px;
        margin: auto;
        border: 1px solid #666;
        padding: 105px 15px 15px 15px;
        display: flex;
        justify-content: space-between;
        background-color: #222222;
        position: relative;
        overflow: hidden;
        
        .title{
            &--main{
                position: absolute;
                top: 20px;
                left: 0;
                color: #fdca30;
                font-size: 24px;
                text-transform: uppercase;
                width: 100%;
                text-align: center;
                font-weight: bold;
            }
        }
    }
    &-data{
        display: flex;
        flex-direction: column;
        margin-left: 20px;
        
        &__price, &__percent, &__capitalization{
            font-size: 30px;
            margin-bottom: 30px;
            color: #ffed00;
            
            .title{
                color: #ef5734;
            }
        }
        &__indicator{
            width: 120px;
            height: 120px;
            background-size: cover;
            
            &.growth{
                background-image: url('../img/graph_growth.svg');
                
            }
            &.decrease{
                background-image: url('../img/graph_decrease.svg');
            }
        }
    }
}
.list-enter-active, .list-leave-active {
  transition: all 1s;
}
.list-enter, .list-leave-to  {
    opacity: 0;
}
.list-leave-to{
    position: absolute;
}
.list-before-enter{
    transform: translatey(0);
}
.list-enter{
    transform: translateY(100%);
    position: absolute;
}
@media screen and (max-width: 530px){
    .crypto{
        &-currencyes{
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: space-between;
            .button{
                font-size: 16px;
                width: 160px;
                &.active{
                    &:nth-child(2n){
                        &:before{
                            animation: .2s activeBtnAnimAdaptive forwards;
                            
                            @keyframes activeBtnAnimAdaptive{
                                from{
                                    right: -100px;
                                }
                                to{
                                    right: -20px;
                                }
                            }
                        }
                    }
                }
                
            }
        }
        &-info-wrapper{
            flex-direction: column;
        }
    }
}
@media screen and (max-width: 410px){
    .crypto{
        &-currencyes{
            .button{
                width: 140px;
                height: 60px;
            }
        }
    }
}